
.sec{
    align-items: center;
    text-align: center;
    object-fit: cover;
    background-size: cover;
    margin-top: 4rem;
    background-image:linear-gradient(rgba(0, 0, 0, 0.4),rgba(0,0,0,0.4)),url(imgs/BMI.jpg);
}
.container-hero{
    background-color:var(--color-container);
   
    width: 40rem;
    gap: 2rem;
    margin: 3rem auto;
    border-radius: 1rem;
    border: 1px solid transparent;
    text-align: center;
    transition: var(--transmition);
    padding: 4rem;
}
.container-hero1{
    background-color:var(--color-container);
    display: grid;
    grid-template-columns: 1fr;
    transition: var(--transmition);
    padding: 8rem;
    width: 50rem;
    gap: 2rem;
    margin: 3rem auto;
    color:gray;
    border-radius: 2rem;

}
/*****************************/
.bmi-result{
    color:grey;
}
.statuse{
    color: green;
    font-size: 1.5rem;
    margin-right: 2rem;
}
.bmi-result p{
    margin-right: 2rem;
}
.bmi-result h3{
    color:white;
    margin-right: 2rem;
}

.right-container{
    display: flex;
    flex-direction: column;
}

.left-container h3{
    margin: 2rem;
    color: var(--color-headings);
}
.data{
    display: flex;
    flex-direction: column;
    gap: 1rem;
    align-items: center;
}
.hieght{
    margin-bottom: 3.1rem;
}
 input{
    width: 10rem;
    height: 2.5rem;
    text-align: center;
    background-color: rgba(26, 25, 25, 0.891);
    color: var(--color-headings);
    margin: 1.5rem;

}
.sec button{
    padding: 0.8rem 3rem;
    border: 1px solid transparent;
    font-weight: 600;
    display: inline-block;
    background-color:rgba(247, 110, 12, 0.767);
    color: var(--color-headings);
    margin-left: 5rem;
    margin-bottom: 2rem;
}
.sec h3{
    margin-left: 5rem;
}
button:hover{
    background: transparent;
    color: var(--color-white);
    border-color: var(--color-white);
}
.info{
    display: flex;
    flex-direction: column;
    gap: 1rem;
    align-items: center;
}
.left-container p{
    font-size: 0.8rem;
    color: gray; 
}
.info p{
    font-size: 0.8rem;
    color: gray;
}
.right-container h3{
    margin: 2rem;
    color: var(--color-headings);
}
#select{
    width: 7rem;
    height: 2.5rem;
    background-color: rgba(26, 25, 25, 0.891);
    color: var(--color-headings);
   
}
.age{
    width: 7rem;
    height: 2.5rem; 
    background-color: rgba(26, 25, 25, 0.891);
    color: white;

}

