body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
/* ************************************************************* */
*{
  margin: 0;
  padding: 0;
  outline: 0;
  text-decoration: none;
  list-style: none;
  box-sizing: border-box;
}
:root{
  --color-main:rgb(38, 37, 37);
  --color-headings: rgb(231, 225, 225);
  --color-container:rgba(10, 10, 10, 0.639);
  --color-prim:rgb(21, 21, 21);
  --color-gray:rgb(56, 56, 56);
  --color-white:#fff;
  --color-warning:rgb(243, 51, 51);
  --color-bright: rgb(231, 225, 225);
  --color-yello:rgb(209, 244, 55);
  --color-warning-danger:rgba(247,88,66,0.4);
  --color-black:#000;
  --color-primary:#6c63ff;
  --color-success:#00bf8e;
  --color-bg:#1f2641;
  --color-bg1:#2e3267;
  --color-bg2:#424890;

  --container-width-lg:80%;
  --container-width-md:90%;
  --container-width-sm:94%;

 --transmition:all 400ms ease;   
}
body{
  color: var(--color-white);
  background: var(--color-gray);
  line-height: 1.7;
  font-family: Arial, Helvetica, sans-serif;
}
.container{
  width: var(--container-width-lg);
  margin: 0 auto;
}
section{
  padding: 3rem 0;;
}
section h2{
  text-align: center;
  margin-bottom: 4rem;
}
h1,
h2,h3,h4,h5{
  line-height: 1.2;
}
h3{
  font-size: 1.4rem;
}
.btn{
  padding: 0.8rem 3rem;
  border: 1px solid transparent;
  font-weight: 600;
  display: inline-block;
  background-color:rgba(247, 110, 12, 0.767);
  color: var(--color-headings);
}

/*********************** NAVBAR**************************/
nav{
  width:100vw;
  height: 4rem;
  background-color: transparent;
  position: fixed;
  top: 0;
  z-index: 11;
}
nav h4{
  margin-left: 8rem;
}
.nav_container{
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
}
.nav_menue{
  display: flex;
  align-items: center;
  gap: 4rem;
}
.nav_menue a{
  font-size: 1rem;
  color: rgb(231, 225, 225);
}
.nav_menue a:hover{
color: var(--color-headings);
transition: var(--transmition);
}
nav button{
color: var(--color-white);
border: none;
}
/**************************************/
@media screen and (max-width:1024px) {
    .container{
      width: var(--container-width-md);
     }
     h1{
      font-size:2.2rem;
     }
     h2{
      font-size: 1.7rem;
     }
     h3{
      font-size: 1.4rem;
     }
     h4{
      font-size: 1.2rem;
     }
     /******************nav*************/
     .open{
      font-size: 1rem;

     }
     nav button{
      display: inline-block;
      background: transparent;
      font-size: 1.8rem;
      color: var(--color-white);
      cursor: pointer;
      margin-right: 2rem;
   
     }
     .nav_menue{
      position: fixed;
      top: 5rem;
      right: -5%;
      height: fit-content;
      width: 18rem;
      flex-direction: column;
      gap: 0;
      display: none; 
     }
        .nav_menue li{
         width: 100%;
         height: 5rem;
         animation: animativeItems 400ms linear forwards;
         opacity: 0; 
         transform-origin: top right;        
 
        }
        .nav_menue li a{
         background: var(--color-main);
         box-shadow: -4rem 6rem 10rem rgba(0,0,0,0.6);
         width: 100%;
         height: 100%;
         display: grid;
         place-items: center;
        }
        .nav_menue li a:hover{
         background: var(--color-prim);
         color: var(--color-white);
        }
               /***********ANIMATION****************/
     @keyframes animativeItems {
      0%{
  transform: rotate2(-90deg) rotatex(90deg) scale(0.1);
      }
      100%{
          transform: rotate2(0) rotatex() scale(1);
          opacity: 1;
  
      }
     }
     .nav_menue li:nth-child(2){
      animation-delay: 200ms;
     }
     .nav_menue li:nth-child(3){
      animation-delay: 400ms;
     }
     .nav_menue li:nth-child(4){
      animation-delay: 600ms;
     }
}
@media screen and (max-width:600px) {
  .container{
      width: var(--container-width-sm);
  }
  .container-hero{
      grid-template-columns: 1fr;
      width: 25rem;
  }
  .container-hero1{
      grid-template-columns: 1fr;
      width: 25rem;
  }
  .nav_menue a{
      display: none;
  }
 .nav-menue{
  right: 2%;
  }
      .footer_container{
          grid-template-columns: 1fr;
          text-align: center;
          gap: 2rem;
          padding: 0;
          
      }
      .footer_1 p{
          margin: 1rem auto;
      }
     
}
/************************************FOOTER**********************************/
.footer{
  background: var(--color-main);
  padding-top: 5rem;
  margin-top: 5rem;

}
.footer_container{
  display: grid;
  grid-template-columns: repeat(3,1fr);
  gap:5rem;
  
}
.footer_container > div h4{
  margin-bottom: 1.2rem;
}
.footer_1 p{
  margin: 0 0 2rem;
}
footer ul li{
  margin-bottom: 0.7rem;
}
footer a{
  color: var(--color-headings);
}
footer ul li a:hover{
  text-decoration: underline;

}
.footer_copyright{
  text-align: center;
  margin-top:4rem ;
  padding: 1.2rem 0;
  border-top: 1px solid var(--color-white);

}
/*****************media*********************/
